<template>
  <div class="heima-analysis">
    <div class="page-header">
      <h1> </h1>
      <p></p>
    </div>

    <el-card class="control-panel">
      <el-row :gutter="20" align="middle">
        <el-col :span="6">
          <div class="control-item">
            <label></label>
            <trading-date-picker
              v-model="selectedDate"
              placeholder=""
              format="YYYY-MM-DD"
              value-format="YYYY-MM-DD"
              style="width: 100%"
            />
          </div>
        </el-col>
        <el-col :span="6">
          <div class="control-item">
            <label></label>
            <el-checkbox v-model="filterOptions.excludeST" style="display: block; margin-bottom: 8px;">
              ST
            </el-checkbox>
            <el-checkbox v-model="filterOptions.includeNonMainBoard">
              
            </el-checkbox>
          </div>
        </el-col>
        <el-col :span="6">
          <div class="control-item">
            <label>&nbsp;</label>
            <el-button type="primary" @click="analyzeHeima" :loading="loading" style="width: 100%">
              <el-icon><Lightning /></el-icon>
              
            </el-button>
          </div>
        </el-col>
      </el-row>
    </el-card>

    <el-card v-if="heimaData.limit_up_stocks?.length > 0">
      <template #header>
        <div style="display: flex; justify-content: space-between; align-items: center;">
          <span> ({{ heimaData.total || 0 }})</span>
          <el-text type="info" size="small">
             
          </el-text>
        </div>
      </template>
      
      <el-table
        :data="heimaData.limit_up_stocks"
        stripe
        :default-sort="{prop: '', order: 'descending'}"
      >
        <el-table-column prop="" label="" />
        <el-table-column prop="" label="" />
        <el-table-column prop="" label="" align="right">
          <template #default="{ row }">
            <span class="price">{{ formatNumber(row.) }}</span>
          </template>
        </el-table-column>
        <el-table-column prop="" label="" align="right" sortable>
          <template #default="{ row }">
            <span class="up-color">{{ formatPercent(row.) }}</span>
          </template>
        </el-table-column>
        <el-table-column prop="" label="" align="right" sortable>
          <template #default="{ row }">
            <span>{{ formatAmount(row.) }}</span>
          </template>
        </el-table-column>
        <el-table-column prop="" label="" align="right" sortable>
          <template #default="{ row }">
            <span>{{ formatPercent(row.) }}</span>
          </template>
        </el-table-column>
        <el-table-column prop="" label="" align="center" sortable :sort-method="sortByConsecutiveDays">
          <template #default="{ row }">
            <el-tag :type="getConsecutiveTagType(row.)">
              {{ row. || '' }}
            </el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="5" label="5" align="right" sortable>
          <template #default="{ row }">
            <span :class="row['5'] >= 0 ? 'up-color' : 'down-color'">
              {{ formatPercent(row['5']) }}
            </span>
          </template>
        </el-table-column>
        <el-table-column prop="10" label="10" align="right" sortable>
          <template #default="{ row }">
            <span :class="row['10'] >= 0 ? 'up-color' : 'down-color'">
              {{ formatPercent(row['10']) }}
            </span>
          </template>
        </el-table-column>
      </el-table>
    </el-card>

    <el-empty v-else-if="!loading" description="" />
  </div>
</template>

<script>
import { ref, onMounted } from 'vue'
import { ElMessage } from 'element-plus'
import ApiService, { utils } from '../services/api'
import TradingDatePicker from '@/components/TradingDatePicker.vue'

export default {
  name: 'HeimaAnalysis',
  components: {
    TradingDatePicker
  },
  setup() {
    const selectedDate = ref(new Date().toISOString().split('T')[0])
    const loading = ref(false)
    const heimaData = ref({})
    const filterOptions = ref({
      excludeST: true,        // ST
      includeNonMainBoard: false  // 
    })

    const analyzeHeima = async () => {
      if (!selectedDate.value) {
        ElMessage.warning('')
        return
      }

      try {
        loading.value = true
        const response = await ApiService.getHeimaAnalysis(selectedDate.value, filterOptions.value)

        // axios
        // axiosdataresponse{success, data, timestamp}
        if (response && response.success && response.data) {
          heimaData.value = {
            limit_up_stocks: response.data,
            total: response.data.length
          }
          console.log(' ', response.data.length, '')
        } else {
          heimaData.value = { limit_up_stocks: [], total: 0 }
          ElMessage.warning('')
        }
      } catch (error) {
        console.error(':', error)
        ElMessage.error(': ' + error.message)
        heimaData.value = { limit_up_stocks: [], total: 0 }
      } finally {
        loading.value = false
      }
    }

    const formatNumber = (value) => utils.formatNumber(value)
    const formatPercent = (value) => utils.formatPercent(value)
    const formatAmount = (value) => utils.formatAmount(value)

    const getConsecutiveTagType = (consecutiveDays) => {
      if (!consecutiveDays) return 'info'

      // 
      const extractBoardCount = (str) => {
        if (str.includes('')) return 1
        const boardMatch = str.match(/(\d+)/)
        return boardMatch ? parseInt(boardMatch[1]) : 0
      }

      const boardCount = extractBoardCount(consecutiveDays.toString())

      if (boardCount >= 5) return 'danger'    // 5
      if (boardCount >= 3) return 'warning'   // 3-4
      if (boardCount >= 2) return 'success'   // 2
      return 'info'                           // 1
    }

    const sortByConsecutiveDays = (a, b) => {
      // 
      const extractBoardCount = (str) => {
        if (!str) return 0

        // 
        // : "XY" -> Y
        // : "11" -> 1, "52" -> 2, "1512" -> 12

        if (str.includes('')) return 1

        // "Y"Y
        const boardMatch = str.match(/(\d+)/)
        if (boardMatch) {
          return parseInt(boardMatch[1])
        }

        // 0
        return 0
      }

      const boardCountA = extractBoardCount(a.)
      const boardCountB = extractBoardCount(b.)

      // 
      return boardCountB - boardCountA
    }

    // 
    const initializeLatestDate = async () => {
      try {
        const response = await ApiService.getLatestMarketDate()
        if (response.success && response.data.latest_date) {
          selectedDate.value = response.data.latest_date
          console.log(` DEBUG: : ${selectedDate.value}`)
        }
      } catch (error) {
        console.warn(':', error)
        // 
      }
    }

    onMounted(async () => {
      console.log(' ')
      await initializeLatestDate()
    })

    return {
      selectedDate,
      loading,
      heimaData,
      filterOptions,
      analyzeHeima,
      formatNumber,
      formatPercent,
      formatAmount,
      getConsecutiveTagType,
      sortByConsecutiveDays
    }
  }
}
</script>

