<template>
  <div class="baima-analysis">
    <div class="page-header">
      <h1> </h1>
      <p></p>
    </div>

    <el-card class="control-panel">
      <el-row :gutter="20" align="middle">
        <el-col :span="6">
          <div class="control-item">
            <label></label>
            <div style="display: flex; flex-wrap: wrap; gap: 10px;">
              <el-checkbox v-model="filterOptions.includeST">ST</el-checkbox>
              <el-checkbox v-model="filterOptions.includeNonMainBoard"></el-checkbox>
            </div>
          </div>
        </el-col>
        <el-col :span="6">
          <div class="control-item">
            <label></label>
            <el-select v-model="selectedQuickIntervals" multiple placeholder="" style="width: 100%">
              <el-option label="30" value="30d" />
              <el-option label="90" value="90d" />
              <el-option label="" value="ytd" />
              <el-option label="6" value="6m" />
            </el-select>
          </div>
        </el-col>
        <el-col :span="8">
          <div class="control-item">
            <label></label>
            <el-row :gutter="10">
              <el-col :span="6">
                <el-date-picker
                  v-model="customInterval.startDate"
                  type="date"
                  placeholder=""
                  format="YYYY-MM-DD"
                  value-format="YYYY-MM-DD"
                  style="width: 100%"
                />
              </el-col>
              <el-col :span="6">
                <el-date-picker
                  v-model="customInterval.endDate"
                  type="date"
                  placeholder=""
                  format="YYYY-MM-DD"
                  value-format="YYYY-MM-DD"
                  style="width: 100%"
                />
              </el-col>
              <el-col :span="6">
                <el-input
                  v-model="customInterval.name"
                  placeholder=""
                  style="width: 100%"
                />
              </el-col>
              <el-col :span="6">
                <el-button @click="addCustomInterval" :disabled="!canAddCustomInterval" style="width: 100%">
                  
                </el-button>
              </el-col>
            </el-row>
          </div>
        </el-col>
        <el-col :span="4">
          <div class="control-item">
            <label>&nbsp;</label>
            <el-button type="primary" @click="analyzeBaima" :loading="loading" style="width: 100%">
              <el-icon><Star /></el-icon>
              
            </el-button>
          </div>
        </el-col>
      </el-row>

      <!--  -->
      <el-row v-if="customIntervals.length > 0" style="margin-top: 15px;">
        <el-col :span="24">
          <div class="control-item">
            <label></label>
            <div style="display: flex; flex-wrap: wrap; gap: 8px;">
              <el-tag
                v-for="(interval, index) in customIntervals"
                :key="index"
                closable
                @close="removeCustomInterval(index)"
                type="info"
              >
                {{ interval.name }} ({{ interval.start_date }}  {{ interval.end_date }})
              </el-tag>
            </div>
          </div>
        </el-col>
      </el-row>
    </el-card>

    <!--  -->
    <el-card v-if="baimaData.stocks?.length > 0" class="stats-panel">
      <el-row :gutter="20">
        <el-col :span="8">
          <el-statistic title="" :value="baimaData.total_count || 0" />
        </el-col>
        <el-col :span="8">
          <el-statistic title="" :value="baimaData.intervals?.length || 0" />
        </el-col>
        <el-col :span="8">
          <el-statistic title="" :value="baimaData.change_columns?.length || 0" />
        </el-col>
      </el-row>
    </el-card>

    <!--  -->
    <el-card v-if="baimaData.stocks?.length > 0">
      <template #header>
        <div style="display: flex; justify-content: space-between; align-items: center;">
          <span> ({{ baimaData.stocks.length }})</span>
          <el-input
            v-model="searchText"
            placeholder=""
            style="width: 200px;"
            clearable
          >
            <template #prefix>
              <el-icon><Search /></el-icon>
            </template>
          </el-input>
        </div>
      </template>

      <el-table
        :data="filteredStocks"
        stripe
        :default-sort="{ prop: defaultSortColumn, order: 'descending' }"
        style="width: 100%"
        height="600"
      >
        <el-table-column prop="" label="" width="80" fixed="left" />
        <el-table-column prop="" label="" width="120" fixed="left" />
        <el-table-column prop="" label="" width="150" show-overflow-tooltip sortable>
          <template #default="{ row }">
            <span v-if="row.">{{ row. }}</span>
            <span v-else style="color: #909399;">-</span>
          </template>
        </el-table-column>


        <!--  -->
        <el-table-column
          v-for="column in baimaData.change_columns"
          :key="column"
          :prop="column"
          :label="column.replace('', '')"
          width="120"
          align="right"
          sortable
        >
          <template #default="{ row }">
            <span
              :class="getChangeClass(row[column])"
              v-if="row[column] !== null && row[column] !== undefined"
            >
              {{ formatPercent(row[column]) }}
            </span>
            <span v-else>-</span>
          </template>
        </el-table-column>
      </el-table>
    </el-card>

    <el-empty v-else-if="!loading" description="" />
  </div>
</template>

<script>
import { ref, onMounted, computed } from 'vue'
import { ElMessage } from 'element-plus'
import { Star, Search } from '@element-plus/icons-vue'
import ApiService, { utils } from '../services/api'

export default {
  name: 'BaimaAnalysis',
  components: {
    Star,
    Search
  },
  setup() {
    const loading = ref(false)
    const baimaData = ref({})
    const searchText = ref('')
    const selectedQuickIntervals = ref(['30d', '90d'])

    const filterOptions = ref({
      includeST: false,           // ST
      includeNonMainBoard: false  // 
    })

    // 
    const customInterval = ref({
      startDate: '',
      endDate: '',
      name: ''
    })

    const customIntervals = ref([])

    // 
    const defaultSortColumn = computed(() => {
      if (baimaData.value.change_columns && baimaData.value.change_columns.length > 0) {
        return baimaData.value.change_columns[0]
      }
      return ''
    })

    // 
    const canAddCustomInterval = computed(() => {
      return customInterval.value.startDate &&
             customInterval.value.endDate &&
             customInterval.value.name.trim()
    })

    // 
    const filteredStocks = computed(() => {
      if (!baimaData.value.stocks) return []

      if (!searchText.value) return baimaData.value.stocks

      const search = searchText.value.toLowerCase()
      return baimaData.value.stocks.filter(stock =>
        (stock. && stock..toLowerCase().includes(search)) ||
        (stock. && stock..toLowerCase().includes(search))
      )
    })

    // 
    const addCustomInterval = () => {
      if (!canAddCustomInterval.value) return

      customIntervals.value.push({
        start_date: customInterval.value.startDate,
        end_date: customInterval.value.endDate,
        name: customInterval.value.name.trim()
      })

      // 
      customInterval.value = {
        startDate: '',
        endDate: '',
        name: ''
      }
    }

    // 
    const removeCustomInterval = (index) => {
      customIntervals.value.splice(index, 1)
    }

    // 
    const generateIntervals = () => {
      const intervals = []
      const today = new Date()

      // 
      selectedQuickIntervals.value.forEach(interval => {
        let startDate, endDate, name

        switch (interval) {
          case '30d':
            startDate = new Date(today.getTime() - 30 * 24 * 60 * 60 * 1000)
            endDate = today
            name = '30'
            break
          case '90d':
            startDate = new Date(today.getTime() - 90 * 24 * 60 * 60 * 1000)
            endDate = today
            name = '90'
            break
          case '6m':
            startDate = new Date(today.getTime() - 180 * 24 * 60 * 60 * 1000)
            endDate = today
            name = '6'
            break
          case 'ytd':
            startDate = new Date(today.getFullYear(), 0, 1)
            endDate = today
            name = ''
            break
        }

        if (startDate && endDate) {
          intervals.push({
            start_date: startDate.toISOString().split('T')[0],
            end_date: endDate.toISOString().split('T')[0],
            name: name
          })
        }
      })

      // 
      customIntervals.value.forEach(interval => {
        intervals.push({
          start_date: interval.start_date,
          end_date: interval.end_date,
          name: interval.name
        })
      })

      return intervals
    }

    const analyzeBaima = async () => {
      // 
      const intervals = generateIntervals()

      if (intervals.length === 0) {
        ElMessage.warning('')
        return
      }

      try {
        loading.value = true

        // 
        const requestData = {
          intervals: intervals,
          min_market_cap: 1,  // 1
          exclude_st: !filterOptions.value.includeST,  // 
          include_main_board: true,  // 
          include_kcb_cyb: true,     // /
          include_bjs: true,         // 
          include_non_main_board: filterOptions.value.includeNonMainBoard
        }

        console.log(' :', requestData)

        const response = await ApiService.postBaimaAnalysis(requestData)

        if (response && response.success && response.data) {
          const backendData = response.data

          if (backendData.error) {
            ElMessage.error(': ' + backendData.error)
            baimaData.value = {}
          } else {
            baimaData.value = backendData
            console.log(' ', backendData.total_count || 0, '')

            // 
            if (backendData.stocks && backendData.stocks.length > 0) {
              const sampleStock = backendData.stocks[0]
              console.log(' DEBUG : :', Object.keys(sampleStock))
              console.log(' DEBUG : :', sampleStock)
              if (sampleStock.) {
                console.log(' DEBUG : :', sampleStock.)
              } else {
                console.log(' DEBUG :  ')
              }
            }

            ElMessage.success(` ${backendData.total_count || 0} `)
          }
        } else {
          baimaData.value = {}
          ElMessage.warning('')
        }
      } catch (error) {
        console.error(':', error)
        ElMessage.error(': ' + error.message)
        baimaData.value = {}
      } finally {
        loading.value = false
      }
    }

    // 
    const formatPercent = (value) => {
      if (value === null || value === undefined) return '-'
      return (value >= 0 ? '+' : '') + value.toFixed(2) + '%'
    }

    const formatMarketCap = (value) => {
      if (!value) return '-'
      return (value / 100000000).toFixed(1)
    }

    const getChangeClass = (value) => {
      if (value === null || value === undefined) return ''
      return value >= 0 ? 'up-color' : 'down-color'
    }

    onMounted(() => {
      console.log(' ')
    })

    return {
      loading,
      baimaData,
      searchText,
      selectedQuickIntervals,
      filterOptions,
      customInterval,
      customIntervals,
      canAddCustomInterval,
      defaultSortColumn,
      filteredStocks,
      addCustomInterval,
      removeCustomInterval,
      analyzeBaima,
      formatPercent,
      getChangeClass
    }
  }
}
</script>

