        if (response.data && response.data.data && Array.isArray(response.data.data)) {
          sectors = response.data.data
        } else if (response.data && Array.isArray(response.data)) {
          sectors = response.data
        }

        if (sectors && sectors.length > 0) {
          // None
          const totalSectors = sectors.length
          const upSectors = sectors.filter(s => s. !== null && parseFloat(s. || 0) > 0).length
          const downSectors = sectors.filter(s => s. !== null && parseFloat(s. || 0) < 0).length
          const flatSectors = totalSectors - upSectors - downSectors

          // 
          sectorData.value = {
            top_sectors: sectors,
            summary: {
              total_sectors: totalSectors,
              up_sectors: upSectors,
              down_sectors: downSectors,
              flat_sectors: flatSectors
            }
          }

          console.log(':', sectors.length, '')
