    const deleteStockGroup = async (group) => {
      try {
        await ElMessageBox.confirm(
          ` "${group.name}" `,
          '',
          {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }
        )

        const response = await fetch(`/api/stock-groups/${group.id}`, {
          method: 'DELETE'
        })

        const data = await response.json()

        if (data.success) {
          ElMessage.success(data.message)
          await loadStockGroups()
        } else {
          ElMessage.error(': ' + data.error)
        }
      } catch (error) {
        if (error !== 'cancel') {
          ElMessage.error(': ' + error.message)
        }
      }
    }

    const formatDate = (dateString) => {
      return new Date(dateString).toLocaleDateString('zh-CN')
    }

    // 
    watch(showLoadGroupDialog, (newVal) => {
      if (newVal) {
        loadStockGroups()
      }
    })

    onMounted(() => {
      loadData()
    })

    return {
      sectorData,
      charts,
      loading,
      globalDate,
      includeSectors,
      includeConcepts,
      // 
      selectedQuickIntervals,
      customInterval,
      customIntervals,
      canAddCustomInterval,
      allIntervals,
      addCustomInterval,
      removeCustomInterval,
      generateIntervals,
      // 
      showCustomPeriod,
      summary,
      topSectors,
      selectedIndex,
      sectorKlineChart,
      sectorKlineData,
      sectorKlineOption,
      klineLoading,
      // 
      selectedSector,
      selectedStock,
      sectorStocks,
      stocksLoading,
      stockKlineChart,
      stockKlineData,
      stockKlineOption,
      stockKlineLoading,
      chartTitle,
      selectSector,
      selectStock,
      loadSectorStocks,
      loadStockKline,
      getSectorRowClass,
      getStockRowClass,
      // 
      stockSearchQuery,
      stockSearchResults,
      selectedStocks,
      stockComparisonChart,
      comparisonLoading,
      normalizeComparison,
      comparisonDays,
      timeRangeType,
      customStartDate,
      customEndDate,
      isTimeRangeValid,
      onStockSearch,
      searchStocks,
      isStockSelected,
      addStock,
      removeStock,
      clearAllStocks,
      generateStockComparison,
      // 
      showSaveGroupDialog,
      showLoadGroupDialog,
      savingGroup,
      loadingGroups,
      stockGroups,
      saveGroupForm,
      saveStockGroup,
      loadStockGroup,
      deleteStockGroup,
      formatDate,
      loadData,
      loadAllData,
      onGlobalDateChange,
      onFilterChange,
      onCustomPeriodChange,
      calculateCustomPeriod,
      loadSectorKline,
      formatNumber,
      formatPercent,
      formatMarketCap,
      formatAmount,
      formatVolume,
      formatVolumeRatio,
      sortByNumber
    }
  }
}
