<template>
  <div class="new-high-stocks">
    <!--  -->
    <div class="page-header">
      <h1> </h1>
    </div>

    <!--  -->
    <el-card class="control-panel">
      <el-row :gutter="20" align="middle">
        <el-col :xs="24" :sm="8" :md="6">
          <div class="control-item">
            <label></label>
            <trading-date-picker
              v-model="selectedDate"
              placeholder=""
              format="YYYY-MM-DD"
              value-format="YYYY-MM-DD"
              style="width: 100%"
            />
          </div>
        </el-col>

        <el-col :xs="24" :sm="8" :md="6">
          <div class="control-item">
            <label></label>
            <el-select v-model="selectedPeriod" placeholder="" style="width: 100%">
              <el-option label="5" :value="5" />
              <el-option label="10" :value="10" />
              <el-option label="20" :value="20" />
              <el-option label="60" :value="60" />
            </el-select>
          </div>
        </el-col>

        <el-col :xs="24" :sm="8" :md="6">
          <div class="control-item">
            <label></label>
            <el-checkbox v-model="filterOptions.excludeST" style="display: block; margin-bottom: 8px;">
              ST
            </el-checkbox>
            <el-checkbox v-model="filterOptions.includeNonMainBoard">
              
            </el-checkbox>
          </div>
        </el-col>

        <el-col :xs="24" :sm="8" :md="6">
          <div class="control-item">
            <label>&nbsp;</label>
            <el-button
              type="primary"
              @click="analyzeNewHigh"
              :loading="analyzing"
              style="width: 100%"
            >
              <el-icon><Search /></el-icon>
              
            </el-button>
          </div>
        </el-col>

        <el-col :xs="24" :sm="24" :md="6">
          <div class="result-summary" v-if="stockList.length > 0">
            <el-tag type="success" size="large">
               {{ stockList.length }} 
            </el-tag>
          </div>
        </el-col>
      </el-row>
    </el-card>

    <!--  -->
    <el-row :gutter="20" v-if="stockList.length > 0">
      <!--  -->
      <el-col :xs="24" :lg="14">
        <el-card class="stock-list-card">
          <template #header>
            <div class="card-header">
              <span>{{ selectedPeriod }}</span>
              <el-button type="text" @click="exportData">
                <el-icon><Download /></el-icon>
                
              </el-button>
            </div>
          </template>

          <el-table 
            :data="stockList" 
            stripe 
            highlight-current-row
            @current-change="handleStockSelect"
            style="width: 100%"
            max-height="600"
          >
            <el-table-column prop="" label="" width="100" fixed="left">
              <template #default="{ row }">
                <el-button type="text" @click="selectStock(row)">
                  {{ row. }}
                </el-button>
              </template>
            </el-table-column>
            
            <el-table-column prop="" label="" width="80" />
            
            <el-table-column prop="" label="" width="80" align="right">
              <template #default="{ row }">
                <span class="price">{{ formatNumber(row.) }}</span>
              </template>
            </el-table-column>
            
            <el-table-column prop="" label="" width="100" align="right">
              <template #default="{ row }">
                <span :class="getChangeClass(row.)">
                  {{ formatPercent(row.) }}
                </span>
              </template>
            </el-table-column>
            
            <el-table-column prop="5(%)" label="5" width="100" align="right" sortable>
              <template #default="{ row }">
                <span :class="getChangeClass(row['5(%)'])">
                  {{ formatPercent(row['5(%)']) }}
                </span>
              </template>
            </el-table-column>

            <el-table-column prop="10(%)" label="10" width="100" align="right" sortable>
              <template #default="{ row }">
                <span :class="getChangeClass(row['10(%)'])">
                  {{ formatPercent(row['10(%)']) }}
                </span>
              </template>
            </el-table-column>
            
            <el-table-column prop="20(%)" label="20" width="100" align="right" sortable>
              <template #default="{ row }">
                <span :class="getChangeClass(row['20(%)'])">
                  {{ formatPercent(row['20(%)']) }}
                </span>
              </template>
            </el-table-column>

            <el-table-column prop="" label="" width="100" align="right">
              <template #default="{ row }">
                <span class="volume">{{ formatVolume(row.) }}</span>
              </template>
            </el-table-column>
          </el-table>
        </el-card>
      </el-col>

      <!-- K -->
      <el-col :xs="24" :lg="10">
        <el-card class="kline-card">
          <template #header>
            <div class="card-header">
              <span v-if="selectedStock">
                {{ selectedStock. }} ({{ selectedStock. }}) K
              </span>
              <span v-else>K</span>
            </div>
          </template>

          <div class="kline-container">
            <div v-if="!selectedStock" class="empty-state">
              <el-empty description="" />
            </div>
            
            <div v-else-if="loadingKline" class="loading-state">
              <el-skeleton :rows="8" animated />
            </div>
            
            <div v-else-if="klineData.length > 0" class="kline-chart">
              <v-chart 
                :option="klineOption" 
                :style="{ height: '400px', width: '100%' }"
                autoresize
              />
            </div>
            
            <div v-else class="error-state">
              <el-empty description="K" />
            </div>
          </div>

          <!--  -->
          <div v-if="selectedStock" class="stock-info">
            <el-descriptions :column="2" size="small" border>
              <el-descriptions-item label="">
                {{ selectedStock. }}
              </el-descriptions-item>
              <el-descriptions-item label="">
                {{ selectedStock. }}
              </el-descriptions-item>
              <el-descriptions-item label="">
                <span class="price">{{ formatNumber(selectedStock.) }}</span>
              </el-descriptions-item>
              <el-descriptions-item label="">
                <span :class="getChangeClass(selectedStock.)">
                  {{ formatPercent(selectedStock.) }}
                </span>
              </el-descriptions-item>
            </el-descriptions>
          </div>
        </el-card>
      </el-col>
    </el-row>

    <!--  -->
    <el-card v-if="!analyzing && stockList.length === 0" class="empty-card">
      <el-empty description="" />
    </el-card>
  </div>
</template>

<script>
import { ref, onMounted, computed, watch } from 'vue'
import { use } from 'echarts/core'
import { CanvasRenderer } from 'echarts/renderers'
import { LineChart, CandlestickChart } from 'echarts/charts'
import {
  TitleComponent,
  TooltipComponent,
  LegendComponent,
  GridComponent,
  DataZoomComponent
} from 'echarts/components'
import VChart from 'vue-echarts'
import ApiService, { utils } from '../services/api'
import TradingDatePicker from '@/components/TradingDatePicker.vue'

// ECharts
use([
  CanvasRenderer,
  LineChart,
  CandlestickChart,
  TitleComponent,
  TooltipComponent,
  LegendComponent,
  GridComponent,
  DataZoomComponent
])

export default {
  name: 'NewHighStocks',
  components: {
    VChart,
    TradingDatePicker
  },
  setup() {
    // 
    const selectedDate = ref(new Date().toISOString().split('T')[0])
    const selectedPeriod = ref(5)
    const analyzing = ref(false)
    const stockList = ref([])
    const selectedStock = ref(null)
    const loadingKline = ref(false)
    const klineData = ref([])
    const filterOptions = ref({
      excludeST: true,        // ST
      includeNonMainBoard: false  // 
    })

    // 
    const formatNumber = computed(() => utils.formatNumber)
    const formatPercent = computed(() => utils.formatPercent)
    const formatVolume = computed(() => utils.formatVolume)

    const klineOption = computed(() => {
      if (klineData.value.length === 0) return {}

      const dates = klineData.value.map(item => item.date)
      const candlestickData = klineData.value.map(item => [
        item.open, item.close, item.low, item.high
      ])
      const volumeData = klineData.value.map(item => item.volume)
      const ma5Data = klineData.value.map(item => item.ma5)
      const ma10Data = klineData.value.map(item => item.ma10)
      const ma20Data = klineData.value.map(item => item.ma20)

      return {
        title: {
          text: `${selectedStock.value?.} K`,
          left: 'center'
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'cross'
          }
        },
        legend: {
          data: [
            {
              name: 'MA5',
              icon: 'rect',
              itemStyle: {
                color: '#4ECDC4'
              }
            },
            {
              name: 'MA10',
              icon: 'rect',
              itemStyle: {
                color: '#ffbf00'
              }
            },
            {
              name: 'MA20',
              icon: 'rect',
              itemStyle: {
                color: '#f92672'
              }
            }
          ],
          top: 30,
          right: 10
        },
        grid: [
          {
            left: '10%',
            right: '8%',
            height: '50%'
          },
          {
            left: '10%',
            right: '8%',
            top: '70%',
            height: '16%'
          }
        ],
        xAxis: [
          {
            type: 'category',
            data: dates,
            scale: true,
            boundaryGap: false,
            axisLine: { onZero: false },
            splitLine: { show: false },
            min: 'dataMin',
            max: 'dataMax'
          },
          {
            type: 'category',
            gridIndex: 1,
            data: dates,
            scale: true,
            boundaryGap: false,
            axisLine: { onZero: false },
            axisTick: { show: false },
            splitLine: { show: false },
            axisLabel: { show: false },
            min: 'dataMin',
            max: 'dataMax'
          }
        ],
        yAxis: [
          {
            scale: true,
            splitArea: {
              show: true
            }
          },
          {
            scale: true,
            gridIndex: 1,
            splitNumber: 2,
            axisLabel: { show: false },
            axisLine: { show: false },
            axisTick: { show: false },
            splitLine: { show: false }
          }
        ],
        dataZoom: [
          {
            type: 'inside',
            xAxisIndex: [0, 1],
            start: 50,
            end: 100
          },
          {
            show: true,
            xAxisIndex: [0, 1],
            type: 'slider',
            top: '85%',
            start: 50,
            end: 100
          }
        ],
        series: [
          {
            name: 'K',
            type: 'candlestick',
            data: candlestickData,
            itemStyle: {
              color: '#ef232a',
              color0: '#14b143',
              borderColor: '#ef232a',
              borderColor0: '#14b143'
            }
          },
          {
            name: 'MA5',
            type: 'line',
            data: ma5Data,
            smooth: true,
            lineStyle: {
              color: '#4ECDC4',
              width: 1
            },
            itemStyle: {
              color: '#4ECDC4'  // 
            },
            symbol: 'none'
          },
          {
            name: 'MA10',
            type: 'line',
            data: ma10Data,
            smooth: true,
            lineStyle: {
              color: '#ffbf00',
              width: 1
            },
            itemStyle: {
              color: '#ffbf00'  // 
            },
            symbol: 'none'
          },
          {
            name: 'MA20',
            type: 'line',
            data: ma20Data,
            smooth: true,
            lineStyle: {
              color: '#f92672',
              width: 1
            },
            itemStyle: {
              color: '#f92672'  // 
            },
            symbol: 'none'
          },
          {
            name: '',
            type: 'bar',
            xAxisIndex: 1,
            yAxisIndex: 1,
            data: volumeData.map((volume, index) => {
              const klineItem = candlestickData[index]
              const isUp = klineItem && klineItem[1] >= klineItem[0] // close >= open
              return {
                value: volume,
                itemStyle: {
                  color: isUp ? '#ef232a' : '#14b143'
                }
              }
            })
          }
        ]
      }
    })

    // 
    const analyzeNewHigh = async () => {
      if (!selectedDate.value) {
        ElMessage.warning('')
        return
      }

      try {
        analyzing.value = true
        console.log(` ${selectedPeriod.value}...`)

        const response = await ApiService.getNewHighStocks(selectedDate.value, selectedPeriod.value, filterOptions.value)
        stockList.value = response.data.stocks || []

        console.log(`  ${stockList.value.length} `)
      } catch (error) {
        console.error(' :', error)
        stockList.value = []
      } finally {
        analyzing.value = false
      }
    }

    const selectStock = async (stock) => {
      if (selectedStock.value?. === stock.) return

      selectedStock.value = stock
      await loadKlineData(stock.)
    }

    const handleStockSelect = (currentRow) => {
      if (currentRow) {
        selectStock(currentRow)
      }
    }

    const loadKlineData = async (stockCode) => {
      try {
        loadingKline.value = true
        console.log(`  ${stockCode} K: ${selectedDate.value}`)

        // APIdata
        const response = await ApiService.getStockKline(stockCode, 30, selectedDate.value, 'data')
        // response.data.data.kline_data
        klineData.value = response.data?.data?.kline_data || []

        console.log(`  ${stockCode} K: ${klineData.value.length} `)
      } catch (error) {
        console.error(`  ${stockCode} K:`, error)
        klineData.value = []
      } finally {
        loadingKline.value = false
      }
    }

    const getChangeClass = (change) => {
      if (change > 0) return 'up-color'
      if (change < 0) return 'down-color'
      return 'neutral-color'
    }

    const exportData = () => {
      // 
      console.log('')
    }

    // 
    const initializeLatestDate = async () => {
      try {
        const response = await ApiService.getLatestMarketDate()
        if (response.success && response.data.latest_date) {
          selectedDate.value = response.data.latest_date
          console.log(` DEBUG: : ${selectedDate.value}`)
        }
      } catch (error) {
        console.warn(':', error)
        // 
      }
    }

    // K
    watch(selectedDate, (newDate, oldDate) => {
      if (newDate !== oldDate && selectedStock.value) {
        console.log(` : ${oldDate}  ${newDate}K`)
        loadKlineData(selectedStock.value.)
      }
    })

    // 
    onMounted(async () => {
      console.log(' ')
      await initializeLatestDate()
    })

    return {
      selectedDate,
      selectedPeriod,
      analyzing,
      stockList,
      selectedStock,
      loadingKline,
      klineData,
      klineOption,
      filterOptions,
      formatNumber,
      formatPercent,
      formatVolume,
      analyzeNewHigh,
      selectStock,
      handleStockSelect,
      loadKlineData,
      getChangeClass,
      exportData
    }
  }
}
</script>

