}

.result-summary {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
}

.stock-list-card,
.kline-card {
  height: 700px;
}

.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.kline-container {
  height: 500px;
}

.empty-state,
