<template>
  <div class="index-analysis">
    <div class="page-header">
      <h1> </h1>
    </div>

    <!--  -->
    <div class="control-panel">
      <el-row :gutter="20">
        <el-col :span="8">
          <trading-date-picker
            v-model="selectedDate"
            placeholder=""
            format="YYYY-MM-DD"
            value-format="YYYYMMDD"
            @change="loadAnalysisData"
          />
        </el-col>
        <el-col :span="8">
          <el-slider
            v-model="daysRange"
            :min="30"
            :max="365"
            :step="30"
            show-input
            @change="loadKlineData"
          />
          <span class="slider-label">: {{ daysRange }}</span>
        </el-col>
        <el-col :span="8">
          <el-button type="primary" @click="refreshData" :loading="loading">
            <i class="el-icon-refresh"></i> 
          </el-button>
        </el-col>
      </el-row>
    </div>

    <!-- 50 -->
    <div class="sentiment-analysis">
      <el-row :gutter="20">
        <!-- 50 -->
        <el-col :span="8">
          <el-card class="sentiment-card">
            <template #header>
              <div class="card-header">
                <span> 50</span>
              </div>
            </template>
            <div v-if="beijingData.change_pct !== null">
              <div class="metric-value" :class="getChangeClass(beijingData.change_pct)">
                {{ beijingData.change_pct?.toFixed(2) }}%
              </div>
              <div class="sentiment-status" v-html="beijingData.status"></div>
            </div>
            <div v-else class="no-data"> </div>
          </el-card>
        </el-col>

        <!--  -->
        <el-col :span="8">
          <el-card class="sentiment-card">
            <template #header>
              <div class="card-header">
                <span> </span>
              </div>
            </template>
            <div v-if="microcapData.change_pct !== null">
              <div class="metric-value" :class="getChangeClass(microcapData.change_pct)">
                {{ microcapData.change_pct?.toFixed(2) }}%
              </div>
              <div class="sentiment-status" v-html="microcapData.status"></div>
            </div>
            <div v-else class="no-data"> </div>
          </el-card>
        </el-col>

        <!--  -->
        <el-col :span="8">
          <el-card class="strategy-card">
            <template #header>
              <div class="card-header">
                <span> </span>
              </div>
            </template>
            <div class="strategy-content">
              <div class="strategy-emoji">{{ strategy.emoji }}</div>
              <div class="strategy-text" :class="getStrategyClass(strategy.risk_level)">
                {{ strategy.strategy }}
              </div>
              <div class="strategy-description">{{ strategy.description }}</div>
            </div>
          </el-card>
        </el-col>
      </el-row>
    </div>

    <!--  -->
    <div class="index-selection">
      <el-card>
        <template #header>
          <div class="card-header">
            <span> K</span>
            <el-button type="text" @click="loadAvailableIndices" :loading="loadingIndices">
              
            </el-button>
          </div>
        </template>
        
        <div class="index-selector">
          <el-checkbox-group v-model="selectedIndices" @change="loadKlineData">
            <el-checkbox 
              v-for="index in availableIndices" 
              :key="index.code" 
              :label="index.name"
              :disabled="!index.available"
            >
              {{ index.name }}
              <span v-if="!index.available" class="unavailable-tag">()</span>
            </el-checkbox>
          </el-checkbox-group>
        </div>
      </el-card>
    </div>

    <!-- K -->
    <div class="kline-chart">
      <el-card v-loading="loadingKline">
        <template #header>
          <div class="card-header">
            <span> K</span>
            <span class="selected-count"> {{ selectedIndices.length }} </span>
          </div>
        </template>
        
        <div v-if="chartOptions && chartOptions.type === 'multiple'" class="multiple-charts-container">
          <div
            v-for="chart in chartOptions.charts"
            :key="chart.name"
            class="single-chart-container"
          >
            <v-chart
              :ref="`chart-${chart.name}`"
              :option="chart.option"
              :style="{ height: '500px', width: '100%', marginBottom: '20px' }"
              autoresize
            />
          </div>
        </div>
        <div v-else-if="chartOptions && Object.keys(chartOptions).length > 0" class="chart-container">
          <v-chart
            ref="klineChart"
            :option="chartOptions"
            :style="{ height: '600px', width: '100%' }"
            autoresize
          />
        </div>
        <div v-else-if="klineHtml" class="chart-container">
          <div ref="chartContainer" v-html="klineHtml"></div>
        </div>
        <div v-else-if="!loadingKline" class="no-chart">
          <el-empty description="K" />
        </div>
      </el-card>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import { use } from 'echarts/core'
import { CanvasRenderer } from 'echarts/renderers'
import { LineChart, CandlestickChart, BarChart } from 'echarts/charts'
import {
  TitleComponent,
  TooltipComponent,
  LegendComponent,
  GridComponent,
  DataZoomComponent,
  ToolboxComponent
} from 'echarts/components'
import VChart from 'vue-echarts'
import TradingDatePicker from '@/components/TradingDatePicker.vue'

// ECharts
use([
  CanvasRenderer,
  LineChart,
  CandlestickChart,
  BarChart,
  TitleComponent,
  TooltipComponent,
  LegendComponent,
  GridComponent,
  DataZoomComponent,
  ToolboxComponent
])

export default {
  name: 'IndexAnalysis',
  components: {
    VChart,
    TradingDatePicker
  },
  data() {
    return {
      loading: false,
      loadingKline: false,
      loadingIndices: false,
      selectedDate: new Date().toISOString().slice(0, 10), //  YYYY-MM-DD 
      daysRange: 180,
      
      // 50
      beijingData: {
        change_pct: null,
        status: '...'
      },
      microcapData: {
        change_pct: null,
        status: '...'
      },
      
      // 
      strategy: {
        strategy: '...',
        emoji: '',
        risk_level: 'unknown',
        description: '...'
      },
      
      // 
      availableIndices: [],
      selectedIndices: ['', '2000', ''],
      klineHtml: '',
      chartOptions: null
    }
  },
  
  computed: {
    //  YYYY-MM-DD  YYYYMMDD 
    formattedDate() {
      if (!this.selectedDate) return ''
      return this.selectedDate.replace(/-/g, '')
    }
  },
  
  mounted() {
    this.initData()
  },
  
  methods: {
    async initData() {
      await this.loadAvailableIndices()
      await this.loadAnalysisData()
      await this.loadKlineData()
    },
    
    async loadAnalysisData() {
      this.loading = true
      try {
        const response = await axios.get('/api/indices/analysis', {
          params: {
            date: this.formattedDate
          }
        })
        
        if (response.data.success) {
          const data = response.data.data
          this.beijingData = data.beijing_data.beijing_50
          this.microcapData = data.beijing_data.microcap
          this.strategy = data.strategy
        }
      } catch (error) {
        console.error(':', error)
        this.$message.error('')
      } finally {
        this.loading = false
      }
    },
    
    async loadAvailableIndices() {
      this.loadingIndices = true
      try {
        const response = await axios.get('/api/indices/available')
        
        if (response.data.success) {
          this.availableIndices = response.data.data.available_indices
        }
      } catch (error) {
        console.error(':', error)
        this.$message.error('')
      } finally {
        this.loadingIndices = false
      }
    },
    
    async loadKlineData() {
      if (this.selectedIndices.length === 0) {
        this.klineHtml = ''
        return
      }

      this.loadingKline = true
      try {
        console.log(' K...', {
          date: this.formattedDate,
          days_range: this.daysRange,
          indices: this.selectedIndices
        })

        const response = await axios.post('/api/indices/kline', {
          indices: this.selectedIndices,
          date: this.formattedDate,
          days_range: this.daysRange,
          format: 'chart'
        })

        console.log(' K:', response.data)

        if (response.data.success) {
          // 
          const klineData = response.data.data?.kline_data || {}
          const chartHtml = klineData.chart_html
          const chartOptions = klineData.chart_options

          console.log(' KHTML:', chartHtml ? chartHtml.length : 0)
          console.log(' :', chartOptions ? '' : '')

          // EChartsHTML
          if (chartOptions) {
            this.chartOptions = this.processChartOptions(chartOptions)
            this.klineHtml = ''
            console.log(' ECharts')
          } else if (chartHtml && chartHtml.length > 0) {
            this.klineHtml = chartHtml
            this.chartOptions = null
            console.log(' HTML')
            // Vue
            this.$nextTick(() => {
              console.log(' Vue nextTick DOM')
              this.executeChartScripts()
            })
          } else {
            console.warn(' ')
          }
        } else {
          console.error(' K:', response.data)
        }
      } catch (error) {
        console.error(' K:', error)
        this.$message.error('K')
      } finally {
        this.loadingKline = false
      }
    },
    
    async refreshData() {
      await this.initData()
      this.$message.success('')
    },

    processChartOptions(options) {
      /**
       * JavaScript
       */
      const processObject = (obj) => {
        if (Array.isArray(obj)) {
          return obj.map(item => processObject(item))
        } else if (obj && typeof obj === 'object') {
          const processed = {}
          for (const [key, value] of Object.entries(obj)) {
            if (key === '__js_function__' && typeof value === 'string') {
              // JavaScript
              try {
                processed[key.replace('__js_function__', '')] = new Function('return ' + value)()
                delete processed['__js_function__']
              } catch (e) {
                console.error('JavaScript:', e)
                processed[key] = value
              }
            } else if (typeof value === 'object' && value !== null && value.__js_function__) {
              // __js_function__
              try {
                processed[key] = new Function('return ' + value.__js_function__)()
              } catch (e) {
                console.error('JavaScript:', e)
                processed[key] = processObject(value)
              }
            } else {
              processed[key] = processObject(value)
            }
          }
          return processed
        }
        return obj
      }

      console.log(' JavaScript...')
      const processedOptions = processObject(options)
      console.log(' JavaScript')
      return processedOptions
    },

    executeChartScripts() {
      // HTMLJavaScript
      this.$nextTick(() => {
        const container = this.$refs.chartContainer
        if (container) {
          const scripts = container.querySelectorAll('script')
          scripts.forEach(script => {
            try {
              // script
              const newScript = document.createElement('script')
              newScript.textContent = script.textContent
              document.head.appendChild(newScript)
              document.head.removeChild(newScript)
              console.log(' ')
            } catch (error) {
              console.error(' :', error)
            }
          })
        }
      })
    },

    getChangeClass(changePct) {
      if (changePct > 0) return 'positive'
      if (changePct < 0) return 'negative'
      return 'neutral'
    },
    
    getStrategyClass(riskLevel) {
      const classMap = {
        '': 'strategy-aggressive',
        '': 'strategy-positive',
        '': 'strategy-neutral',
        '': 'strategy-cautious',
        '': 'strategy-conservative',
        '': 'strategy-wait'
      }
      return classMap[riskLevel] || 'strategy-neutral'
    }
  }
}
</script>

