<script>
import { ref, reactive, onMounted, watch, computed } from 'vue'
import { ElMessage, ElMessageBox } from 'element-plus'
import { Search, Download, Refresh, TrendCharts, Loading, Warning, QuestionFilled } from '@element-plus/icons-vue'
import axios from 'axios'
import VChart from 'vue-echarts'
import { use } from 'echarts/core'
import { CanvasRenderer } from 'echarts/renderers'
import { CandlestickChart, LineChart, BarChart } from 'echarts/charts'
import {
  TitleComponent,
  TooltipComponent,
  LegendComponent,
  GridComponent,
  DataZoomComponent,
  ToolboxComponent
} from 'echarts/components'
import ApiService, { utils } from '@/services/api'
import TradingDatePicker from '@/components/TradingDatePicker.vue'

use([
  CanvasRenderer,
  CandlestickChart,
  LineChart,
  BarChart,
  TitleComponent,
  TooltipComponent,
  LegendComponent,
  GridComponent,
  DataZoomComponent,
  ToolboxComponent
])

export default {
  name: 'MoneyEffect',
  components: {
    Search,
    Download,
    Refresh,
    TrendCharts,
    Loading,
    Warning,
    QuestionFilled,
    VChart,
    TradingDatePicker
  },
  setup() {
    // 
    const selectedDate = ref(new Date().toISOString().split('T')[0])
    const analyzing = ref(false)
    const refreshing = ref(false)
    const allStocksList = ref([])      // 300
    const strongStocksList = ref([])   // 3
    const selectedStock = ref(null)
    const klineData = ref([])          // K
    const klineLoading = ref(false)
    const allStatsData = ref(null)     // 
    const strongStatsData = ref(null)  // 
    const filterOptions = ref({
      excludeST: true,        // ST
      includeNonMainBoard: false  // 
    })

    // APIURL
    const API_BASE = 'http://localhost:5000'

    // 
    const formatNumber = computed(() => utils.formatNumber)
    const formatPercent = computed(() => utils.formatPercent)
    const formatVolume = computed(() => utils.formatVolume)

    const klineOption = computed(() => {
      if (klineData.value.length === 0) return {}

      const dates = klineData.value.map(item => item.date)
      const candlestickData = klineData.value.map(item => [
        item.open, item.close, item.low, item.high
      ])

      const volumeData = klineData.value.map(item => {
        const volume = item.amount || item.volume || 0
        return {
          value: volume,
          itemStyle: {
            color: item.close >= item.open ? '#ef232a' : '#14b143'
          }
        }
      })

      // MA
      const ma5Data = klineData.value.map(item => item.ma5)
      const ma10Data = klineData.value.map(item => item.ma10)
      const ma20Data = klineData.value.map(item => item.ma20)

      return {
        animation: false,
        color: ['#4ECDC4', '#ffbf00', '#f92672'],
        title: {
          text: selectedStock.value ? `${selectedStock.value.} (${selectedStock.value.})` : 'K',
          left: 'center',
          textStyle: {
            color: '#333',
            fontSize: 16
          }
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'cross'
          },
          formatter: function(params) {
            let result = `${params[0].axisValue}<br/>`
            params.forEach(param => {
              if (param.seriesName === 'K') {
                const data = param.data
                result += `: ${data[1]}<br/>: ${data[2]}<br/>: ${data[3]}<br/>: ${data[4]}<br/>`
              } else if (param.seriesName === '') {
                result += `: ${utils.formatVolume(param.value)}<br/>`
              } else {
                result += `${param.seriesName}: ${param.value}<br/>`
              }
            })
            return result
          }
        },
        legend: {
          data: [ 'MA5', 'MA10', 'MA20', ],
          top: 30,
          right: 10
        },
        grid: [
          {
            left: '10%',
            right: '8%',
            height: '50%'
          },
          {
            left: '10%',
            right: '8%',
            top: '70%',
            height: '16%'
          }
        ],
        xAxis: [
          {
            type: 'category',
            data: dates,
            scale: true,
            boundaryGap: false,
            axisLine: { onZero: false },
            splitLine: { show: false },
            splitNumber: 20,
            min: 'dataMin',
            max: 'dataMax'
          },
          {
            type: 'category',
            gridIndex: 1,
            data: dates,
            scale: true,
            boundaryGap: false,
            axisLine: { onZero: false },
            axisTick: { show: false },
            splitLine: { show: false },
            axisLabel: { show: false },
            splitNumber: 20,
            min: 'dataMin',
            max: 'dataMax'
          }
        ],
        yAxis: [
          {
            scale: true,
            splitArea: {
              show: true
            }
          },
          {
            scale: true,
            gridIndex: 1,
            splitNumber: 2,
            axisLabel: { show: false },
            axisLine: { show: false },
            axisTick: { show: false },
            splitLine: { show: false }
          }
        ],
        dataZoom: [
          {
            type: 'inside',
            xAxisIndex: [0, 1],
            start: 50,
            end: 100
          },
          {
            show: true,
            xAxisIndex: [0, 1],
            type: 'slider',
            top: '85%',
            start: 50,
            end: 100
          }
        ],
        series: [
          {
            name: 'K',
            type: 'candlestick',
            data: candlestickData,
            itemStyle: {
              color: '#ef232a',
              color0: '#14b143',
              borderColor: '#ef232a',
              borderColor0: '#14b143'
            }
          },
          {
            name: 'MA5',
            type: 'line',
            data: ma5Data,
            smooth: true,
            symbol: 'none',
            lineStyle: {
              opacity: 0.8,
              color: '#4ECDC4'
            },
            itemStyle: {
              color: '#4ECDC4'  // 
            }
          },
          {
            name: 'MA10',
            type: 'line',
            data: ma10Data,
            smooth: true,
            symbol: 'none',
            lineStyle: {
              opacity: 0.8,
              color: '#ffbf00'
            },
            itemStyle: {
              color: '#ffbf00'  // 
            }
          },
          {
            name: 'MA20',
            type: 'line',
            data: ma20Data,
            smooth: true,
            symbol: 'none',
            lineStyle: {
              opacity: 0.8,
              color: '#f92672'
            },
            itemStyle: {
              color: '#f92672'  // 
            }
          },
          {
            name: '',
            type: 'bar',
            xAxisIndex: 1,
            yAxisIndex: 1,
            data: volumeData
          }
        ]
      }
    })

    //  - 
    const analyzeMoneyEffect = async () => {
      if (!selectedDate.value) {
        ElMessage.warning('')
        return
      }

      analyzing.value = true
      try {
        console.log(' ...', {
          date: selectedDate.value
        })

        // 
        const [allStocksResponse, strongStocksResponse] = await Promise.all([
          axios.get(`${API_BASE}/api/money-effect`, {
            params: {
              date: selectedDate.value,
              type: 'all',
              exclude_st: filterOptions.value.excludeST,
              include_non_main_board: filterOptions.value.includeNonMainBoard
            }
          }),
          axios.get(`${API_BASE}/api/money-effect`, {
            params: {
              date: selectedDate.value,
              type: 'strong',
              exclude_st: filterOptions.value.excludeST,
              include_non_main_board: filterOptions.value.includeNonMainBoard
            }
          })
        ])

        // 
        if (allStocksResponse.data.success) {
          allStocksList.value = allStocksResponse.data.stocks || []
          allStatsData.value = allStocksResponse.data.stats || {}
          console.log(' ', {
            stockCount: allStocksList.value.length
          })
        } else {
          allStocksList.value = []
          allStatsData.value = null
        }

        // 
        if (strongStocksResponse.data.success) {
          strongStocksList.value = strongStocksResponse.data.stocks || []
          strongStatsData.value = strongStocksResponse.data.stats || {}
          console.log(' ', {
            stockCount: strongStocksList.value.length
          })
        } else {
          strongStocksList.value = []
          strongStatsData.value = null
        }

        // 
        const totalStocks = allStocksList.value.length + strongStocksList.value.length
        if (totalStocks > 0) {
          ElMessage.success(`${allStocksList.value.length}${strongStocksList.value.length}`)
        } else {
          ElMessage.warning('')
        }

      } catch (error) {
        console.error(' :', error)
        ElMessage.error(': ' + (error.response?.data?.message || error.message))
        allStocksList.value = []
        strongStocksList.value = []
        allStatsData.value = null
        strongStatsData.value = null
      } finally {
        analyzing.value = false
      }
    }

    // 
    const selectStock = async (stock) => {
      selectedStock.value = stock
      await loadKlineChart(stock)
    }

    // 
    const handleStockSelect = (currentRow) => {
      if (currentRow) {
        selectStock(currentRow)
      }
    }

    // K
    const loadKlineChart = async (stock) => {
      if (!stock) return

      klineLoading.value = true
      try {
        console.log(' K:', stock., stock.)

        const response = await ApiService.getStockKline(stock., 60)
        // response.data.data.kline_data
        klineData.value = response.data?.data?.kline_data || []

        console.log(`  ${stock.} K: ${klineData.value.length} `)
      } catch (error) {
        console.error(' K:', error)
        ElMessage.error('K: ' + (error.response?.data?.message || error.message))
        klineData.value = []
      } finally {
        klineLoading.value = false
      }
    }

    // 
    const refreshData = async () => {
      refreshing.value = true
      try {
        await analyzeMoneyEffect()
        ElMessage.success('')
      } catch (error) {
        ElMessage.error('')
      } finally {
        refreshing.value = false
      }
    }

    // 
    const exportData = (type = 'all') => {
      let dataToExport = []
      let filename = ''

      if (type === 'all') {
        if (allStocksList.value.length === 0) {
          ElMessage.warning('')
          return
        }
        dataToExport = allStocksList.value
        filename = `_${selectedDate.value}.csv`
      } else if (type === 'strong') {
        if (strongStocksList.value.length === 0) {
          ElMessage.warning('')
          return
        }
        dataToExport = strongStocksList.value
        filename = `_${selectedDate.value}.csv`
      }

      try {
        const csvContent = generateCSV(dataToExport, type)
        downloadCSV(csvContent, filename)
        ElMessage.success('')
      } catch (error) {
        console.error(':', error)
        ElMessage.error('')
      }
    }

    // CSV
    const generateCSV = (data, type = 'all') => {
      let headers = ['', '', '', '', '', '5', '10', '20', '', '']

      // 
      if (type === 'strong') {
        headers = ['', '', '', '', '', '', '5', '10', '20', '', '']
      }

      const csvRows = [headers.join(',')]

      data.forEach(row => {
        const values = headers.map(header => {
          const value = row[header]
          return typeof value === 'string' ? `"${value}"` : value
        })
        csvRows.push(values.join(','))
      })

      return csvRows.join('\n')
    }

    // CSV
    const downloadCSV = (content, filename) => {
      const blob = new Blob(['\ufeff' + content], { type: 'text/csv;charset=utf-8;' })
      const link = document.createElement('a')
      const url = URL.createObjectURL(blob)
      link.setAttribute('href', url)
      link.setAttribute('download', filename)
      link.style.visibility = 'hidden'
      document.body.appendChild(link)
      link.click()
      document.body.removeChild(link)
    }

    // 
    const getChangeClass = (value) => {
      if (value === null || value === undefined) return ''
      const num = Number(value)
      if (num > 0) return 'positive'
      if (num < 0) return 'negative'
      return ''
    }

    // 
    const getBoardTagType = (days) => {
      if (days >= 6) return 'danger'
      if (days >= 4) return 'warning'
      return 'success'
    }

    // 
    watch(selectedDate, () => {
      if (allStocksList.value.length > 0 || strongStocksList.value.length > 0) {
        analyzeMoneyEffect()
      }
    })

    // 
    onMounted(() => {
      analyzeMoneyEffect()
    })

    return {
      selectedDate,
      analyzing,
      refreshing,
      allStocksList,
      strongStocksList,
      selectedStock,
      klineData,
      klineOption,
      klineLoading,
      allStatsData,
      strongStatsData,
      filterOptions,
      analyzeMoneyEffect,
      selectStock,
      handleStockSelect,
      refreshData,
      exportData,
      formatNumber,
      formatPercent,
      formatVolume,
      getChangeClass,
      getBoardTagType
    }
  }
}
</script>

